import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import MoveForward from "@components/common/move-forward";
import CentralizedCryptoScreen from "@screens/centralized-crypto";
import { useLocation } from "@reach/router";


const CentralizedCryptoPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Centralized Crypto Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CentralizedCryptoScreen db={db} />
      </main>
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const location = useLocation();
  const slug = location.pathname.split("/")?.[1];
  const firestore = data?.site?.siteMetadata?.firestore;

  const fileUrl =
    data?.centralizedCryptoExchangeImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;

  const defMetaData1 = data?.seoDataJson;
  const dbMetaData1 = data?.metaJson;


  console.log("defMetaData1::", JSON.stringify(defMetaData1));
  console.log("dbMetaData1::", JSON.stringify(dbMetaData1));

  const defMetaData = defMetaData1.find((item) => item.slug === slug );
  const dbMetaData = dbMetaData1.find((item) => item.slug === slug );
  
  const title = dbMetaData.title || defMetaData.title; 
  const metaTitle = dbMetaData.metaTitle || defMetaData.metaTitle; 
  const description = dbMetaData.description || defMetaData.description;
  
  console.log("CentralizedCryptoPage-seo::");
  
  return (
    <Seo
      title={title}
      metaTitle={metaTitle}
      description={description}
      metaData={dbMetaData || defMetaData}
      image={fileUrl}
      siteFirestore={firestore}
      slug={slug}
    ></Seo>
  );
};

export default CentralizedCryptoPage;

export const queryData = graphql`
  query MyQuery {
    centralizedCryptoExchangeImg: file(
      relativePath: { eq: "centralized-crypto-exchange.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    metaJson {
      slug
      title
      description
      metaTitle
    }
    seoDataJson {
      slug
      title
      description
      metaTitle
    }
  }
`;
